.map {
  position: relative;
}

.map__fallback {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.map__interactive {
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  & [class*="ground-pane"] {
    filter: grayscale(0.5);
  }

  & [class*="copyrights-pane"] {
    display: none;
  }
}
